* {
  box-sizing: border-box;
}
:root {
  --main-black: #292b2f;
  --main-gray: #E8E8E8;
  --main-white: #f3f0f0;
}

.app-wrapper {
  display: grid;
  grid-template-areas: 
  'nav header'
  'nav cont';
  grid-template-rows: 60px 1fr;
  grid-template-columns: 200px 10fr;
  /* grid-gap: 10px; */
}

.app-wrapper-content{
    grid-area: cont;
    background-color: var(--main-gray);
}




